.wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  background-color: #2F2F2F;
  height: 100%;
  width: 100%;

  .intro {
    position: relative;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    background-color: #bb153e;
    max-width: 500pt;
    align-items: center;
    color: #0c1844;
    padding: 40px 25px 0 25px;
    overflow-y: scroll;

    &:before {
      content: '';
      position: absolute;
      overflow: hidden;
      top: 0;
      left: 7%;
      width: 110%;
      height: 100%;
      background-image: url('../images/a_flag.jpg'); /* Путь к вашей картинке */
      background-size: 110% auto; /* Растягиваем по ширине, сохраняя соотношение сторон */
      background-repeat: no-repeat; /* Отключаем повторение фона */
      background-position: top;
      transform-origin: center;
      transform: rotate(-7deg); /* Поворачиваем картинку */
      filter: blur(1px);
      z-index: 1;
      opacity: .4;
    }

    h1 {
      position: relative;
      width: max-content;
      padding: 23pt 30pt;
      font-size: 5em;
      background-image: radial-gradient(rgba(255, 255, 255, .5) 30%, transparent 70%);
      text-shadow: 0 0 25px #FFF, 0 0 45px #fff, 0 0 65px #fff;
      z-index: 2;

      &:before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background: rgba(255, 255, 255, .20);
        filter: blur(10px);
      }
    }

    .quote {
      position: relative;
      width: 100%;
      padding: 20pt;
      text-align: justify;
      font-size: 1.8em;
      font-style: italic;
      font-weight: 350;
      background-image: radial-gradient(rgba(255, 255, 255, .5) 50%, transparent 70%);
      text-shadow: 0 0 25px #FFF, 0 0 45px #fff, 0 0 65px #fff, 0 0 85px #fff;
      z-index: 2;
      backdrop-filter: blur(1px);

      &:before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background: rgba(255, 255, 255, .20);
        filter: blur(10px);
      }
    }

    .fab {
      padding: 30pt 5pt 10pt 5pt;
      color: lightyellow;
      font-size: 1.8em;
      text-align: left;

      p {
        padding: 9pt 0;

      }

      strong {
        font-weight: 900;
      }
    }

    .tabWrapper {
      position: relative;
      display: flex;
      flex-direction: column;
      font-size: 1.8em;
      text-align: center;

      .tab {
        text-align: left;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 5pt;

        div {
          padding: 3pt;
          color: white;
          border-radius: 5pt;
          background-color: #0c1844;

          p {
            padding: 5pt 8pt;
            display: flex;
            flex-direction: column;

            span {
              strong {
                font-weight: 900;
              }
            }
          }
        }
      }
    }

    .testWrapper {
      position: relative;
      display: flex;
      flex-direction: column;
      padding: 1pt;
      background-color: #0c1844;
      border-radius: 5pt;
    }

    .buttonWrapper {
      button {
        background-color: #0c1844;
        font-size: 2.8rem;
        width: max-content;
        padding: .6em 2em;
        border-radius: 8pt;
        color: whitesmoke;
        outline: none;
        margin: .7em 0;
        border: none;
        transition: 1s;
        cursor: pointer;

        &:disabled {
          cursor: not-allowed;
          opacity: 0.3;
        }

        &:active {
          transform: scale(0.6);
        }
      }
    }

    .fabCenter{
      padding: 20pt 5pt 10pt 5pt;
      color: lightyellow;
      font-size: 1.8em;
      text-align: center;
    }

    .remark{
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 1.4em;
      padding: 10pt 5pt 20pt 5pt;

      span {
        text-align: center;
        color: whitesmoke;
      }
    }
  }
}
